import React, {useContext} from 'react';
import styled from 'styled-components';
import Module from "./Module";
import ModuleGroup from "./ModuleGroup";
import {CourseContext} from "../CourseContext";

const Base = styled.div`
    padding: 0 16px;
`;

const Course = props => {
    const loadCourse = course => {
        let groups = [];

        for (let key in course.children) {
            let temp = [];
            let modules = course.children[key].children;
            for (let moduleKey in modules) {
                temp.push(<Module title={moduleKey}
                                  key={moduleKey}
                                  src={modules[moduleKey].pictureUrl}
                                  active={modules[moduleKey].active}
                />)
            }
            groups.push(<ModuleGroup title={key} key={key}>{temp}</ModuleGroup>)
        }

        return groups;
    };

    const courses = useContext(CourseContext);

    return (
        <Base>
            {
                loadCourse(courses[props.course])
            }
        </Base>
    )
};

export default Course;