import React from 'react';
import styled from 'styled-components';
import {CourseContext} from "./CourseContext";
import {Link} from "react-router-dom";

const Carousel = styled.div`
    width: 100%;
    display: flex;
    overflow: auto;
    align-items: stretch;
`;

const CarouselCard = styled.div`
    width: 100%;
    padding: 16px;
    box-sizing: border-box;
    flex-shrink: 0;
`;

const Base = styled.div`
    ${props => props.theme.elevation[3]}
    border-radius: 4px;
    overflow: hidden;
`;

const Title = styled.div`
    padding: 16px;
    padding-bottom: 0;
    color: #666;
    font-size: 14pt;
`;

const Description = styled.div`
    padding: 16px;
`;

const Picture = styled.div`
    background: url(${props => props.pictureUrl}) center center / cover;
    height: 150px;
`;

const CourseOverviewTitle = styled.div`
    padding: 16px;
    font-size: 16pt;
`;

const CardLink = styled.div`
    color: black;
    padding: 0 16px 16px 16px;
    > a {
        text-decoration: none;
        color: black;
    }
`;

const CourseOverview = props => {
    const courses = React.useContext(CourseContext);

    const constructCourseCards = (courses) => {
        let courseCards = [];
        for (let key in courses) {
            courseCards.push(
                <CarouselCard key={key}>
                    <Base>
                        <Picture pictureUrl={courses[key].pictureUrl}/>
                        <Title>{key}</Title>
                        <Description>
                            {courses[key].description}
                        </Description>
                        <CardLink>
                            <Link to={{
                                pathname: '/course',
                                state: key
                            }}>
                                Zum Kurs
                            </Link>
                        </CardLink>
                    </Base>

                </CarouselCard>
            )
        }

        return courseCards;
    };

    return (
        <>
            <CourseOverviewTitle>
                Kurse
            </CourseOverviewTitle>
            <Carousel>
                {constructCourseCards(courses)}
            </Carousel>
        </>
    )
};

export default CourseOverview;