import React from 'react';
import styled from 'styled-components';
import {animated, useSpring} from "react-spring";
import {useMeasure} from './Hooks';

const Base = styled.div`
    position: relative;
    display: inline-block;
    overflow: hidden;
`;

const Shader = animated(styled.div`
    background-image: linear-gradient(90deg, ${props => props.color}00 0, ${props => props.color}ff 100px, ${props => props.color}ff 100%);
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: calc(100% + 100px);
`);

const Fader = ({children, color, transition, active}) => {
    const [measurement, dimensions] = useMeasure();

    const transform = useSpring({
        transform: `translateX(${active ? -100 : dimensions.width}px)`,
    });

    return (
        <Base ref={measurement}>
            {children}
            <Shader color={color} style={transform}/>
        </Base>
    )
};

export default Fader;