import React from 'react';
import styled, {withTheme} from 'styled-components';
import Icon from "./Icon";
import {animated, config, useSpring} from "react-spring";

const Base = animated(styled.div`
    transition: 0.3s ease box-shadow, 0.3s ease background-color;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 12px;
    background-color: ${props => props.active ? props.theme.secondary.light : props.theme.secondary.main};
    color: ${props => props.theme.secondary.color};
    
    ${props => props.active ? props.theme.elevation[7] : props.theme.elevation[5]}
    
    
    ${props => {
    switch (props.size) {
        case 'small' :
            return 'padding: 4px';
        case 'medium':
            return 'padding: 8px';
        case 'large' :
            return 'padding: 16px';
        default:
            return 'padding: 8px';
    }
}}
    
    ${props => props.animated ? '' : `
        transition: 0.1s ease box-shadow, 0.1s ease transform, 0.1s ease background-color;
        :active {
            transform: scale(1.25, 1.25);
            background-color: ${props.theme.secondary.light};
        }
    `}
`);

const FAB = ({size, icon, active, animation, onClick, theme}) => {
    const animated = animation !== undefined;

    animation = animation || {from: 'rotateZ(0deg)', to: 'rotateZ(0deg)'};

    const iconStyle = useSpring({
        transform: active ? animation.to : animation.from,
        config: config.gentle
    });

    const baseStyle = useSpring({
        boxShadow: active ? theme.elevation[7].slice(0, -1) : theme.elevation[5].slice(0, -1),
        transform: active ? 'scale(1.25, 1.25)' : 'scale(1, 1)',
        config: config.gentle
    });


    return (
        <Base style={animated ? baseStyle : {}}
              size={size}
              animated={animated}
              onClick={onClick}
              active={active}>
            <Icon style={animated ? iconStyle : {}} animated={animated}>{icon}</Icon>
        </Base>
    )
};

export default withTheme(FAB);