import React from 'react';
import styled from 'styled-components';
import FAB from "./FAB";
import {animated, config, useSpring} from "react-spring";
import {useClickAway} from "./Hooks";
import Fader from "./Fader";
import {useHistory} from 'react-router-dom';

const Base = styled.div`
    display: inline-block;
    user-select: none;
    position: fixed;
    bottom: 0;
    right: 0;
    z-index: 900;
    padding: 16px;
`;

const Group = animated(styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-bottom: 8px;
    
    > div {
        margin: 4px;
    }
    
    > div:last-child {
        margin: 8px;
    }
`);

const BottomBar = props => {
    const [open, setOpen] = React.useState(false);

    const openStyle = useSpring({
        transform: open ? 'translateY(0)' : 'translateY(200%)',
        config: config.gentle
    });

    const ref = useClickAway(() => setOpen(false));

    const history = useHistory();

    const goHome = () => {
        history.push('/', 'Startseite');
        setOpen(false);
    };

    const goFullScreen = () => {
        const elem = document.documentElement;

        function openFullscreen() {
            if (elem.requestFullscreen) {
                elem.requestFullscreen();
            } else if (elem.mozRequestFullScreen) { /* Firefox */
                elem.mozRequestFullScreen();
            } else if (elem.webkitRequestFullscreen) { /* Chrome, Safari and Opera */
                elem.webkitRequestFullscreen();
            } else if (elem.msRequestFullscreen) { /* IE/Edge */
                elem.msRequestFullscreen();
            }
        }

        setOpen(false);
    };

    return (
        <Base ref={ref}>
            <Group style={openStyle}>
                <Fader/>
                <FAB size={'medium'} icon={'home'} onClick={goHome}/>
                <FAB size={'medium'} icon={'fullscreen'}/>
                <FAB size={'medium'} icon={'local_library'}/>
                <FAB size={'medium'} icon={'note'}/>
            </Group>
            <FAB icon={'add'}
                 size={'large'}
                 animation={{from: 'rotateZ(0deg)', to: 'rotateZ(405deg)'}}
                 active={open}
                 onClick={() => {
                     setOpen(!open);
                 }}
            />
        </Base>
    )
};

export default BottomBar;