import styled from 'styled-components';

const ListItem = styled.li`
    display: flex;
    align-items: center;
    justify-content: space-between;

    transition: 0.3s ease background-color;
    padding: 16px;
    
    :hover {
        background-color: #ccc;
    }
    
    border-top: 1px solid #ccc;
`;

export default ListItem;